/**
 *
 * Link - Type
 *
 * @author Takuto Yanagida
 * @version 2021-12-06
 *
 */


'use strict';

window['NACSS'] = window['NACSS'] || {};


(function (NS) {

	{
		/**
		 *
		 * Type
		 *
		 * @author Takuto Yanagida
		 * @version 2021-12-06
		 *
		 */
		
		
		function apply(as, opts = {}) {
			opts = Object.assign({
				styleLinkImage   : ':ncLinkImage',
				styleLinkSimple  : ':ncLinkSimple',
				styleLinkAnchor  : ':ncLinkAnchor',
				styleLinkExternal: ':ncLinkExternal',
				styleLinkFile    : ':ncLinkFile',
				allowedClasses   : ALLOWED_CLASSES,
				extensionTable   : EXT_TABLE,
				isUrlExternal    : isUrlExternal,
			}, opts);
		
			for (const a of as) {
				addClass(a, a.getAttribute('href'), opts);
			}
		}
		
		function applyByUrl(as, opts = {}) {
			opts = Object.assign({
				styleLinkAnchor  : ':ncLinkAnchor',
				styleLinkExternal: ':ncLinkExternal',
				styleLinkFile    : ':ncLinkFile',
				extensionTable   : EXT_TABLE,
				isUrlExternal    : isUrlExternal,
			}, opts);
		
			for (const a of as) {
				addClassByUrl(a, a.getAttribute('href'), opts);
			}
		}
		
		const ALLOWED_CLASSES = [
			'alignleft',
			'aligncenter',
			'alignright',
			'size-thumbnail',
			'size-small',
			'size-medium-small',
			'size-medium',
			'size-medium-large',
			'size-medium_large',
			'size-large',
			'size-full'
		];
		
		const EXT_TABLE = {
			doc : 'doc',
			docx: 'doc',
			xls : 'xls',
			xlsx: 'xls',
			ppt : 'ppt',
			pptx: 'ppt',
			pdf : 'pdf'
		};
		
		
		// -------------------------------------------------------------------------
		
		
		function addClass(a, url, opts) {
			if (isLinkImage(a, opts)) {
				setClass(a, opts.styleLinkImage);
			} else {
				if (isLinkEmpty(a)) {
					addClassByUrl(a, url, opts);
				} else if (isLinkSimple(a)) {
					setClass(a, opts.styleLinkSimple);
					if (isLinkSimpleUrl(a, url)) {
						setClass(a, opts.styleLinkSimple, true, 'url');
					}
					addClassByUrl(a, url, opts);
				}
			}
		}
		
		function isLinkImage(a, opts) {
			if (a.className) {
				for (const c of a.className.split(' ')) {
					if (!opts.allowedClasses.includes(c)) return false;
				}
			}
			let ok = false;
			for (const c of a.childNodes) {
				if (c.nodeType === 1) {  // ELEMENT_NODE
					if (!ok && c.tagName === 'IMG') {
						ok = true;
					} else {
						ok = false;
						break;
					}
				}
			}
			return ok;
		}
		
		function isLinkSimple(a) {
			if (a.className) return false;
			const cs = a.childNodes;
			if (cs.length === 0) return false;
			for (const c of cs) {
				if (c.className) return false;
				if (c.nodeType === 1) {  // ELEMENT_NODE
					if (c.tagName !== 'BR' && !getComputedStyle(c).display.includes('inline')) {
						return false;
					}
				}
			}
			return true;
		}
		
		function isLinkEmpty(a) {
			return !a.className && a.childNodes.length === 0;
		}
		
		function isLinkSimpleUrl(a, url) {
			return url !== null && url !== '' && a.childNodes.length && a.innerHTML.trim() === url;
		}
		
		
		// -------------------------------------------------------------------------
		
		
		function addClassByUrl(a, url, opts) {
			const outIsUp = [false];
			if (isUrlAnchor(url, a, outIsUp)) {
				setClass(a, opts.styleLinkAnchor, true, outIsUp[0] ? 'up' : '');
			} else if (opts.isUrlExternal(url)) {
				setClass(a, opts.styleLinkExternal);
			}
			const t = getFileType(url, opts.extensionTable);
			if (t) {
				setClass(a, opts.styleLinkFile);
				setClass(a, opts.styleLinkFile, true, t);
			}
		}
		
		function isUrlExternal(url) {
			if (url !== null && url !== '') {
				if (!url.startsWith(`${location.protocol}//${location.host}`)) {
					if (url.match(/^https?:\/\//) || url.match(/^\/\//)) {
						return true;
					}
				}
			}
			return false;
		}
		
		function getFileType(url, extTab) {
			if (url !== null && url !== '' && !url.endsWith('/')) {
				const d = url.indexOf('//');
				if (d !== -1) {
					const s = url.indexOf('/', d + 2);
					url = (s === -1) ? '' : url.substring(s + 1);
				}
				const p = url.lastIndexOf('.');
				if (p !== -1) {
					const ext = url.substring(p + 1).toLowerCase();
					return extTab[ext] ?? null;
				}
			}
			return null;
		}
		
		NS.linkApply      = apply;
		NS.linkApplyByUrl = applyByUrl;

		// Export the function
		NS.linkIsLinkImage = isLinkImage;
	}

	/**
	 *
	 * Style Class Utilities
	 *
	 * @author Takuto Yanagida
	 * @version 2021-11-11
	 *
	 */
	
	
	function hasClass(tar, cls) {
		const key = cls.substr(1);
		if (cls.startsWith(':')) {
			return tar.dataset[key] !== undefined;
		} else {
			return tar.classList.contains(key);
		}
	}
	
	function setClass(tar, cls, enabled = true, val = '') {
		const key = cls.substr(1);
		if (cls.startsWith(':')) {
			if (enabled) {
				tar.dataset[key] = val;
			} else {
				delete tar.dataset[key];
			}
		} else {
			const c = [key, val].join('-');
			if (enabled) {
				tar.classList.add(c);
			} else {
				tar.classList.remove(c);
			}
		}
	}
	
	function getSelector(cls) {
		if (cls.startsWith(':')) {
			return `*[data-${cls.substr(1).replace(/([A-Z])/g, c => '-' + c.charAt(0).toLowerCase())}]`;
		} else {
			return `*${cls}`;
		}
	}
	
	/**
	 *
	 * Common Functions
	 *
	 * @author Takuto Yanagida
	 * @version 2021-11-10
	 *
	 */
	
	
	function isUrlAnchor(url, a = null, outIsUp = null) {
		if (url !== null && url !== '') {
			const p = url.indexOf('#');
			if (p !== -1) {
				const id = url.substring(p + 1);
				if (id.length) {
					const t = document.getElementById(id);
					if (t !== null) {
						if (
							p === 0 ||
							url.substring(0, p) === location.href.replace(location.hash ? location.hash : '#', '')
						) {
							if (a && outIsUp) {
								const from = a.getBoundingClientRect().top;
								const to   = t.getBoundingClientRect().top;
								if (to < from) outIsUp[0] = true;
							}
							return true;
						}
					}
				}
			}
		}
		return false;
	}
	

})(window['NACSS']);
